'use strict'

const test = require('ava')
const sinon = require('sinon')
const proxyquire = require('proxyquire')
const metricFixtures = require('./fixtures/metric')
const agentFixtures = require('./fixtures/agent')

let config = {
  logging: function () { }
}

let AgentStub = {
  hasMany: sinon.spy()
}

// Argumentos para testear
let uuid = 'yyy-yyy-yyy'
let type = 'RAM'
let MetricStub = null
let db = null
let sandbox = null

let typeArgs = {
  where: { type }
}

let uuidArgs = { uuid }

let single = Object.assign({}, metricFixtures.single) // Creando un objeto metric falso

let newMetric = {
  type: 'RAM',
  value: '60 MB'
}

test.beforeEach(async () => {
  sandbox = sinon.createSandbox()
  MetricStub = {
    belongsTo: sandbox.spy()
  }

  // Model Create Stub
  AgentStub.findOne =sandbox.stub()
  AgentStub.findOne.withArgs(uuidArgs).returns(Promise.resolve(agentFixtures.byUuid))

  MetricStub.create = sandbox.stub()
  MetricStub.create.withArgs(newMetric).returns(Promise.resolve({ toJSON () { return newMetric } }))

  // Metric FindAll Stub
  MetricStub.findAll = sandbox.stub()
  MetricStub.findAll.withArgs(AgentStub.uuid).returns(Promise.resolve(metricFixtures.byAgentUuid))
  MetricStub.findAll.withArgs(typeArgs).returns(Promise.resolve(metricFixtures.byType))

  const setupDatabase = proxyquire('../', {
    './models/agent': () => AgentStub,
    './models/metric': () => MetricStub
  })

  db = await setupDatabase(config)
})

test.afterEach(() => {
  sandbox && sandbox.restore()
})

test('Metric', t => {
  t.truthy(db.Metric, 'Metric service should exist')
})

test('Agent', t => {
  t.truthy(db.Agent, 'Agent service should exist')
})

test.serial('Setup', t => {
  t.true(AgentStub.hasMany.called, 'AgentModel.HasMany was executed')
  t.true(AgentStub.hasMany.calledWith(MetricStub), 'Argument should be the Metricmodel')
  t.true(MetricStub.belongsTo.called, 'MetricModel.belongsTo was executed')
  t.true(MetricStub.belongsTo.calledWith(AgentStub), 'Argument should be the Agentmodel')
})

test.serial('Metric#Create', async t => {
  let metric = await db.Metric.create(uuid, single)
  // t.true(AgentStub.findOne.called, 'findOne should be called on AgentModel')
  // t.true(AgentStub.findOne.calledOnce, 'findOne should be called once')
  // t.true(AgentStub.findOne.calledWith(uuidArgs), 'findOne should be called with uuidArgs')
  t.true(MetricStub.create.called, 'create should be called on model')
  t.true(MetricStub.create.calledOnce, 'create should be called once')
  t.true(MetricStub.create.calledWith(newMetric), 'create should be called with args newAgent')

  t.deepEqual(metric, newMetric, 'metric should be the same')
})
